




























import Vue from 'vue';
import { Component, Prop, Watch } from 'vue-property-decorator';
import ImagesPanel from '@/components/ImagesPanel.vue';

function getData(hold: boolean, harddrop: boolean) {
    if (hold) {
        if (harddrop) {
            return import('../assets/data_harddrop_hold.json');
        } else {
            return import('../assets/data_softdrop_hold.json');
        }
    } else {
        if (harddrop) {
            return import('../assets/data_harddrop_nohold.json');
        } else {
            return import('../assets/data_softdrop_nohold.json');
        }
    }
}

@Component({
    components: {
        ImagesPanel,
    },
})
export default class ListPanel extends Vue {
    @Prop() hold!: boolean;
    @Prop() harddrop!: boolean;
    @Prop() airTSD!: boolean;

    private items: { [name in string]: number[] } | null = null;
    private selectedIndex = -1;
    private suffix: string = "";

    @Watch('hold')
    onChangedHold() {
        this.reload();
    }

    @Watch('harddrop')
    onChangedHarddrop() {
        this.reload();
    }

    @Watch('airTSD')
    onChangedAirTSD() {
        this.reload();
    }

    public created() {
        this.reload();
    }

    private reload() {
        this.items = null;
        setTimeout(() => {
            const i = getData(this.hold, this.harddrop);
            i.then((data: any) => {
                const items = data.default as { [name in string]: number[] };
                if (this.airTSD) {
                    this.items = items;
                } else {
                    const filtered: { [name in string]: number[] } = {};
                    for (let key in items) {
                        filtered[key] = items[key].filter(v => v < 82);
                    }
                    this.items = filtered;
                }
            }).catch(console.error);
        }, 10);
    }

    private onClick(index: number) {
        if (this.selectedIndex != index) {
            this.selectedIndex = index;
        } else {
            this.selectedIndex = -1;
        }
    }

    get suffixUpperCase() {
        return this.suffix.toUpperCase();
    }
}
